/* players wrapper */
.players_wrapper{
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-column-gap: 2px;
  grid-row-gap: 2px;
}

/* player */
.player{
  display: -ms-flexbox;
  display: flex;

  position: relative;
  overflow: hidden;

  color: var(--color-white);

  -webkit-transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

  -o-transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

  transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

  background: #606c88;
  background: -webkit-linear-gradient(-45deg, #606c88 0%,#3f4c6b 100%);
  background: -webkit-linear-gradient(315deg, #606c88 0%,#3f4c6b 100%);
  background: -o-linear-gradient(315deg, #606c88 0%,#3f4c6b 100%);
  background: linear-gradient(135deg, #606c88 0%,#3f4c6b 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#606c88', endColorstr='#3f4c6b',GradientType=1 );
}

  .player:hover{
    z-index: 2;
    -webkit-box-shadow: var(--shadow2);
            box-shadow: var(--shadow2);
  }
  .player:hover .player__image{
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-filter: blur(5px) grayscale(100%);
            filter: blur(5px) grayscale(100%);
  }
  .player:hover .player__content{
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  .player:hover .player__content-data{
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
  }

  /* player image */
  .player__image{
    width: 50%;
    height: 350px;
    padding: 20px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

    -webkit-transition: all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

    -o-transition: all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

    transition: all 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);

    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
  }

  .player__image img{
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;

    -webkit-filter: grayscale(40%);

            filter: grayscale(40%);
  }

  /* player content */
  .player__content,
  .player__content-data{
    -webkit-transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -o-transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: all 600ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
  }
  .player__content{
    margin-top: 20px;
  }
  .player__content-data{
    width: 100%;
    height: 100%;

    position: absolute;
    left: 0; top: 0;
    z-index: 4;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

    -webkit-transform: translateY(100%);

        -ms-transform: translateY(100%);

            transform: translateY(100%);
    background-color:rgba(255,255,255, .5);
  }

  /* player name, surname, nickname */
  .player__name{
    margin-top: 10px;
    margin-bottom: 0;
  }
  .player__nickname{
    margin-bottom: 10px;
    display: block;

    font-size: 18px;
    color: var(--color-highlight);
  }
  .divider{
    padding: 10px;
    color: var(--color-gray);
  }



