/* header block */
.info{
  grid-area: info;

  padding: 25px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
}

  /* pretitle */
  .info__pretitle{
    text-transform: uppercase;
    color: var(--color-gray);
  }
  /* title */
  .info__title{
    margin: 10px 0;

    color: var(--color-white);
  }
  /* subtitle */
  .info__subtitle{
    color: var(--color-white);
  }


